.sidebar {
    display: none;
    width: 100%;
}

.sidebar.open {
    display: block;
    width: 250px;
    height: 180%;
    border: 1px solid #000;
    padding: 10px 20px;
    z-index: 4;
    background-color: #fff;
    position: fixed;
}

.background {
    position: absolute;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 3;
}

.nav {
    display: flex;
    flex-direction: column;
}

.nav a {
    color: black;
    font-family: Montserrat;
    font-size: 22px;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    margin-bottom: 20px;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #3D6B99;
}
.close {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-bottom: 10px;
    /* border: 1px solid red; */
    /* color: red; */
}


